import Vue from "vue"
import VueI18n from "vue-i18n"
import zhCN from "./zh-CN"
// (APP, Hallokey) start
import custom_zhCN from "./custom/zh-CN"
// (APP, Hallokey) end
// let zhCN = require("http://oss.thinmoo.com/prod/zh-CN.js");





































import appConfig from "./app-config"
// import arEG from './ar_EG.js'
Vue.use(VueI18n)
let _this = Vue.prototype
//  start
let total_zhCN = _this.$objectAssign(zhCN, custom_zhCN)
//  end

//  start

















//  end

const i18n = new VueI18n({
  locale: "zh-CN",
  messages: {
    "zh-CN": Object.assign(total_zhCN, appConfig), // 


















  },
})

export default i18n
