	readEID(devObj, callback) {
		if (bluetoothPlugin.readEID) {
			_this.$showLoading();
			bluetoothPlugin.readEID(devObj, (result) => {
				_this.$hideLoading();
				let errorCode = result.errorCode;
				if (errorCode === 0) {
					// 
					_this.$showToast("");
					if (callback && typeof callback === "function") {
						callback(result);
					}
				} else if (errorCode === 48) {
					_this.$showToast("");
				} else {
					_this.$showToast("" + errorCode + "");
				}
			});
		} else {
			_this.$showToast("APP");
		}
	}

