	// 
	_getDeviceOnlineStatus(devObj) {
		var that = this;
		var manage = _this.$t("manage");
		if (this.number > 1) {
			this.timer = setTimeout(function() {
				_this.$req.request({
					url: "/devDevice/app/getDeviceOnlineStatus",
					data: {
						"devSn": devObj.devSn,
						"isShowLoad": false
					},
					success: result => {
						var code = result.code;
						if (code === 0 && that.number > 1) {
							var onlineStatus = result.data.onlineStatus;
							if (onlineStatus == 1) {
								_this.$hideLoading();
								_this.$showToast(manage["connect-net"] || "");
								that.number = 6;
							} else {
								that._getDeviceOnlineStatus(devObj);
							}
						} else if (that.number == 1) {
							_this.$hideLoading();
							_this.$showToast(manage["connect-fail"] || "");
							that.number = 6;
						} else {
							that._getDeviceOnlineStatus(devObj);
						}
					},
					fail: () => {
						if (that.number == 1) {
							_this.$hideLoading();
							_this.$showToast(manage["connect-fail"] || "");
							that.number = 6;
						} else {
							that._getDeviceOnlineStatus(devObj);
						}
					},
					complete: () => {
						that.number--;
						uni.log("", that.number);
						// clearTimeout(that.timer);
						// that.timer = null;
					}
				});
			}, 10000);
		}
	}

