	//  (loginType=1,account,loginType=2,account,)
	loginVPhoneServer(account) {
		// var _this = this;
		var sdkI18n = this.sdkI18n;
		if (vphonePlugin.loginVPhoneServer) {
			console.log("123");
			var errorMsgObj = this.getErrMsgObj();
			var obj = {
				"account": account,
				"appId": this.configParam.appId,
				"appSecret": this.configParam.appSecret,
				"loginType": 1,
				"vphoneServerIp": this.configParam.vphoneServerIp,
				"vphoneServerPort": this.configParam.vphoneServerPort
			};

			vphonePlugin.loginVPhoneServer(obj, res => {
				var errorMsg = sdkI18n["login-video-fail"] || "";
				var errorCode = res.errorCode;
				if (errorCode == 0) {
					console.log("", "res============", res);
				} else {
					console.log("", "res============", res);
					for (var i in errorMsgObj) {
						if (i == errorCode) {
							errorMsg = errorMsgObj[i];
							break;
						}
					}
					_this.$showToast(errorMsg + "(" + errorCode + ")");
				}
			});
		} else {
			_this.$showToast(sdkI18n["not-support-logging-in-to-the-audio-and-video-server"] || "app");
		}
	}

	//  callType1callAccountcallType2callAccount
	callAccount(callAccount, callType, account, name) {
		var common = _this.$t("common");
		if (vphonePlugin.callAccount) {
			var errorMsgObj = this.getErrMsgObj();
			_this.$showLoading();
			vphonePlugin.callAccount(callAccount, callType, account, name, result => {
				_this.$hideLoading();
				var errorCode = result.errorCode;
				var errorMsg = _this.$t("common")["call-fail"] || "";
				if (errorCode !== 0) {
					for (var i in errorMsgObj) {
						if (i == errorCode) {
							errorMsg = errorMsgObj[i];
							break;
						}
					}
					_this.$showToast(errorMsg + "(" + errorCode + ")");
				}
			});
		} else {
			_this.$showToast(sdkI18n["app-does-not-support-calling"] || "app");
		}
	}

	// accountaccountType2
	addAccountToBlackList(account, accountType, callback) {
		var sdkI18n = this.sdkI18n;
		if (vphonePlugin.addAccountToBlackList) {
			_this.$showLoading();
			vphonePlugin.addAccountToBlackList(account, accountType, result => {
				_this.$hideLoading();
				var errorCode = result.errorCode;
				var errorMsg = errorCode == 0 ? (_this.$t("common")["add-blacklist-success"] || "") :
					(_this.$t("common")["add-blacklist-fail"] || "");
				_this.$showToast(errorMsg);
				if (result.errorCode == 0) {
					callback(result);
				}
			});
		} else {
			_this.$showToast(sdkI18n["does-not-support-adding-blacklist"] || "app");
		}
	}

