import {
	API_URL
} from '@/env'
import router from '@/common/router'
export default {
	/**
	 * 
	 */
	routerTo(path, params = {}, isTabbar) {
		let objParams = params;
		// 
		if (~path.indexOf('http')) {




			router.push({
				path: '/pages/public/webview',
				query: {
					'webviewPath': path
				}
			})

			return false
		}
		// 
		if (path.indexOf('?') !== -1) {
			let index = path.lastIndexOf('?');
			let query = path.substring(index + 1, path.length);
			let arr = query.split('&')
			path = path.slice(0, index);
			arr.forEach(item => {
				let mArr = []
				let obj = {}
				mArr = item.split('=');
				obj[mArr[0]] = mArr[1];
				objParams = {
					...objParams,
					...obj
				}

			})
		}
		// tabbar
		if (isTabbar) {
			router.replaceAll({
				path: path,
				query: objParams
			})
		} else {
			router.push({
				path: path,
				query: objParams
			})
		}

	},
	/**
	 * fn
	 * param(imgPath): 
	 */

	checkImgHttp(imgPath) {
		let newPath = '';
		let pathArr = imgPath.split('://');





		pathArr[0] = 'https'

		newPath = pathArr.join('://');
		return newPath;
	},
	// 
	callPhone(phoneNumber = '') {
		let num = phoneNumber.toString()
		uni.makePhoneCall({
			phoneNumber: num,
			fail(err) {
				console.log('makePhoneCall', err)
			},
		});
	},
	// -
	chooseImage(count = 1) {
		return new Promise((resolve, reject) => {
			uni.chooseImage({
				count: count, //9
				sizeType: ['original', 'compressed'], //
				sourceType: ['album'], //
				success: res => {
					resolve(res.tempFilePaths);
				}
			});
		}).catch(e => {
			reject(e)
		})
	},
	// -
	uploadImage(api, url) {
		let config_url = API_URL;
		uni.showLoading({
			title: ''
		});
		return new Promise((resolve, reject) => {
			uni.uploadFile({
				url: config_url + api,
				filePath: url,
				name: 'file',
				success: res => {
					res = JSON.parse(res.data);
					if (res.code === 1) {
						uni.hideLoading()
						uni.showToast({
							title: '',
							icon: 'none'
						});
						resolve(res.data)
					} else {
						uni.hideLoading()
						uni.showModal({
							title: '',
							content: res.msg
						});
					}
				}
			});
		}).catch(e => {
			reject(e)
		})
	},
	// -
	previewImage(urls = [], current = 0) {
		uni.previewImage({
			urls: urls,
			current: current,
			indicator: 'default',
			loop: true,
			fail(err) {
				console.log('previewImage', urls, err)
			},
		})
	},
	// -
	getImageInfo(src = '') {
		return new Promise((resolve, reject) => {
			uni.getImageInfo({
				src: src,
				success: (image) => {
					resolve(image)
				},
				fail(err) {
					console.log('getImageInfo', src, err)
				},
			})
		}).catch(e => {
			reject(e)
		})

	},
	/**
	 * 
	 */
	// 
	format(t) {
		let format = {
			d: '00',
			h: '00',
			m: '00',
			s: '00',
		}
		if (t > 0) {
			let d = Math.floor(t / 86400)
			let h = Math.floor((t / 3600) % 24)
			let m = Math.floor((t / 60) % 60)
			let s = Math.floor(t % 60)
			format.d = d < 10 ? '0' + d : d
			format.h = h < 10 ? '0' + h : h
			format.m = m < 10 ? '0' + m : m
			format.s = s < 10 ? '0' + s : s
		}
		return format
	},
	//()
	formatToHours(t) {
		let format = {
			d: '00',
			h: '00',
			m: '00',
			s: '00',
		}
		if (t > 0) {
			let h = Math.floor(t / 3600)
			let m = Math.floor((t / 60) % 60)
			let s = Math.floor(t % 60)

			format.h = h < 10 ? '0' + h : h
			format.m = m < 10 ? '0' + m : m
			format.s = s < 10 ? '0' + s : s
		}
		return format
	},
	// 
	timestamp(timestamp) {
		let date = new Date(timestamp * 1000); //
		let y = date.getFullYear();
		let m = date.getMonth() + 1;
		let d = date.getDate();

		m = m < 10 ? '0' + m : m;
		d = d < 10 ? '0' + d : d

		let dateText = y + "-" + m + "-" + d
		return dateText
	},
	// 
	// "YYYY-mm-dd HH:MM"
	dateFormat(fmt, date) {
		let ret;
		const opt = {
			"Y+": date.getFullYear().toString(), // 
			"m+": (date.getMonth() + 1).toString(), // 
			"d+": date.getDate().toString(), // 
			"H+": date.getHours().toString(), // 
			"M+": date.getMinutes().toString(), // 
			"S+": date.getSeconds().toString() // 
			// 
		};
		for (let k in opt) {
			ret = new RegExp("(" + k + ")").exec(fmt);
			if (ret) {
				fmt = fmt.replace(ret[1], (ret[1].length == 1) ? (opt[k]) : (opt[k].padStart(ret[1].length, "0")))
			};
		};
		return fmt;
	},
	/**
	 *  @fn  
	 *  @param {*} startTime 
	 *  @param {*} endTime 
	 *  @return {string} str 
	 */
	getTimeInterval(startTime, endTime) {
		let runTime = parseInt((endTime - startTime) / 1000);
		let year = Math.floor(runTime / 86400 / 365);
		runTime = runTime % (86400 * 365);
		let month = Math.floor(runTime / 86400 / 30);
		runTime = runTime % (86400 * 30);
		let day = Math.floor(runTime / 86400);
		runTime = runTime % 86400;
		let hour = Math.floor(runTime / 3600);
		runTime = runTime % 3600;
		let minute = Math.floor(runTime / 60);
		runTime = runTime % 60;
		let second = runTime;
		let str = '';
		if (year > 0) {
			str = year + '';
		}
		if (year <= 0 && month > 0) {
			str = month + '';
		}
		if (year <= 0 && month <= 0 && day > 0) {
			str = day + '';
		}
		if (year <= 0 && month <= 0 && day <= 0 && hour > 0) {
			str = hour + '';
		}
		if (year <= 0 && month <= 0 && day <= 0 && hour <= 0 && minute > 0) {
			str = minute + '';
		}
		if (year <= 0 && month <= 0 && day <= 0 && hour <= 0 && minute <= 0 && second > 0) {
			str += second + '';
		}
		str += '';
		return str;
	},


	/**
	 *title()
	 *icon():  successloadingnone
	 *duration(): 0, 
	 *options()
	 */
    msg(title, duration=2000, mask=false, icon='none'){
    	//
    	if(Boolean(title) === false){
    		return;
    	}
    	uni.showToast({
    		title,
    		duration,
    		mask,
    		icon
    	});
    },
	
	//  yyyy-mm-dd H:M:S
	compareDateTime(a, b) {
	    var starttimes = new Date(a).getTime();
	    var lktimes = new Date(b).getTime();
	    if (starttimes >= lktimes) {
			console.log('');
	        return false;
	    }
	    else
	        return true;
	}


}
